body {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: Arial, sans-serif;
}

#video, #canvas {
    width: 640px;
    height: 480px;
    border: 1px solid #ccc;
}

#repCount {
    font-size: 24px;
    margin-top: 20px;
}

.main-container {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    gap: 32px;
}

.video-area {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#debugWindow {
    width: 300px;
    height: 120px;
    overflow-y: auto;
    background: #222;
    color: #fff;
    font-size: 14px;
    padding: 10px;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.2);
}